@import "./../../../styles/variables.scss";

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 42px;
  min-width: 900px;
  &__title {
    font-weight: 800;
    font-size: 1.5rem;
    letter-spacing: $letterSpacing;
    line-height: 30px;

    &.light__mode {
      color: $black;
    }
    &.dark__mode {
      color: $white;
    }
  }
  &__content {
    display: flex;
    align-items: center;
    height: 100%;

    &__links {
      display: flex;
      align-items: center;
      height: 24px;

      .link__icon {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        &:not(last-child) {
          margin-right: 24px;
        }
      }
      .link__icon__notifiction {
        position: relative;
        &::after {
          content: "";
          position: absolute;
          width: 8px;
          height: 8px;
          right: 3px;
          top: 3px;
          background: $green;
          border-radius: 50%;
        }
      }
    }

    &__divider {
      height: 100%;
      width: 1px;
      margin: 0 32px;

      &.light__mode {
        background: $dividerLineLight;
      }
      .dark__mode {
        background: $dividerLine;
      }
    }

    &__profile {
      display: flex;
      align-items: center;

      .profile__name {
        cursor: pointer;
        font-size: 14px;
        font-weight: 600;
        margin-right: 24px;
        letter-spacing: $letterSpacing;
        &.light__mode {
          color: $black;
        }
        &.dark__mode {
          color: $white;
        }
        &:hover {
          text-decoration: underline;
        }
      }
      .profile__img {
        cursor: pointer;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        box-shadow: 0px 0px 3px rbga($dark, 0.3);

        .img {
          border-radius: 50%;
          width: 100%;
          height: auto;
        }
      }
    }
  }
}
